* {
  margin: 0;
  padding: 0;
  /* border: 1px solid #000; */
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1 {
  text-align: center;
  padding: 25px;
}
.content {
  padding: 20px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}
.match-title > span {
  width: 150px;
  text-align: center;
  font-weight: bold;
}
.match-title {
  display: flex;
  justify-content: space-between;
}
.match-content {
  display: flex;
  justify-content: space-between;
}
.r16, .qf, .sf, .final {
  width: 150px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  justify-content: space-around;
}
.match {
  height: 100px;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: center
}
.match-up, .match-down {
  display: flex;
  column-gap: 5px;
}
.right .match .match-up, 
.right .match .match-down {
  flex-direction: row-reverse;
  text-align: right;
}
hr {
  height: 1px;
  margin: 5px 0;
  color: inherit;
  background-color: currentColor;
  border: 0;
  opacity: .25;
}
.match .flag {
  font-size: 22pt;
}
.match .name {
  font-size: 11pt;
  display: flex;
  flex-direction: column;
}
.winner {
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 150px;
}
.social {
  text-align: center;
}
.btn-download {
  padding: 10px 20px;
  cursor: pointer;
}